.react-tabs__tab-list{
    padding: 0;
    margin: 0;
    list-style: none;

    display: -ms-flexbox;

    display: flex;
    font-size: 0.3467rem;
    background: #fff;
    -webkit-box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
            box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}
.react-tabs__tab{
    -ms-flex: 1 1;
        flex: 1 1;
    text-align: center;

    height: 1.3333rem;
    line-height: 1.3333rem;
}
.react-tabs__tab:focus{
    outline: none;
}
.react-tabs__tab--selected{
    color: #ff4949;
}


