.box {
  margin-bottom: 0.2667rem;
  background: #fff;
  border-radius: 0.1067rem; }

.box:last-child {
  margin: 0; }

.box_item {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.4rem;
  padding: 0.4rem;
  position: relative; }
  .box_item::after {
    content: '';
    position: absolute;
    bottom: 0;
    height: 1px;
    background: #eaeaea;
    left: 0;
    right: 0;
    transform: translateY(0.5); }
  .box_item:last-child::after {
    content: none; }

.box_icon {
  width: 0.5867rem;
  height: 0.4667rem;
  margin-right: 0.4rem; }



