.loading_layer {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0; }

.loading_wrap {
  position: absolute;
  left: 50%;
  top: 50%;
  padding: 0.4rem;
  transform: translate(-50%, -50%);
  border-radius: 3px;
  background: rgba(0, 0, 0, 0.3); }

.loading {
  width: .8rem !important;
  height: .8rem !important; }



